.sidebar{
    display: flex;
    flex-direction: column;
    flex: 0.35;
}
.sidebar__header{
    display: flex;
    justify-content: space-between;
    padding: 20px;
    border-right: 1px solid lightgrey;
}
.sidebar__headerRight{
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-right: 1px solid lightgrey;
    min-width: 10vw;
}
.sidebar__headerRight > .MuiSvgIcon-root{
    margin-right: 2vw;
    font-size: 24px !important;
}
.sidebar__search{
    display: flex;
    align-items: center;
    background-color: #f6f6f6;
    height: 50px;
    padding: 10px!important;
}
.sidebar__searchContainer{
    display: flex;
    align-items: center;
    background-color: white;
    width: 100%;
    height: 35px;
    padding: 10px;
    border-radius: 20px;
    
}
/* .sidebar__searchContainer > .MuiSvgIcon-root{
    color: gray;
    padding: 10px;
} */

.sidebar__searchContainer > input{
    border: none;
    outline-width: 0;
    width: 100%;
    margin-left: 10px;
}
.sidebar__chats{
    flex: 1;
    overflow: scroll;
    background-color: #fff;
}